import browser from "webextension-polyfill"
import runEvent from './events'
import './security.js'
let products = false

browser.runtime.onMessage.addListener(async (eventString, sender, sendResponse) => {
  const {name, data} = JSON.parse(eventString)
    if(name == 'product-list') {
        products = data
        return false
    }

    if(name == 'legacy-login'){
      console.log('login işlemi...')
      chrome.browsingData.remove({
        "origins": [data.url]
      }, {
        "cacheStorage": true,
        "cookies": true,
        "fileSystems": true,
        "indexedDB": true,
        "localStorage": true,
        "serviceWorkers": true,
        "webSQL": true
      }, ()=> {
        chrome.tabs.create({ url: data.url }, function(newTab) {
          var newTabId = newTab.id;
          chrome.tabs.onUpdated.addListener(function listener(tabId, changeInfo) {
            setTimeout(()=> {
              var messageToSend = { event: 'legacy-login', product_key: data.product_key };
              chrome.tabs.sendMessage(newTabId, messageToSend);
              chrome.tabs.onUpdated.removeListener(listener);
            }, 1324)
          });
        });
      });

    
      return;
    }

    if(name == 'change-proxy') {
      return false
    }

    if(name == 'open-popup') {
        chrome.browserAction.openPopup();
        return false
    }

    if(name == 'clean-cookies'){
      console.log('çerez silme şeysi..')
    }

    if(name == 'close-tabs'){
      chrome.windows.getAll({populate: true}, function(windows) {
        windows.forEach(function(window) {
          chrome.windows.remove(window.id);
        });
      });
    }

    if(name == 'close-current-tab'){
      chrome.tabs.query({}, function(tabs) {
        tabs.forEach(function(tab) {
          if (tab.url.includes(data)) {
            chrome.tabs.remove(tab.id, function() {
              console.log('Tab closed due to keepa.com URL:', tab.url);
            });
          }
        });
      });
    }

    return await runEvent(name, data, sendResponse, 'background')
});



async function waitSerialKey()
{
  return new Promise((resolve)=> {
    chrome.storage.local.get(['serial_key'], function(result) {
      resolve(result.serial_key || undefined)
    });
  })
}

async function gbuySetCookie()
{
  const currentKey = await waitSerialKey()
  if(currentKey != undefined) {
    chrome.cookies.set({
      url: "https://gbuy.cheap",
      name: "current_serial_key",
      value: currentKey
    }, function(cookie) {
  
    });
  }
  setInterval(()=> {
    if(currentKey == undefined) {
      return;
    }
    chrome.cookies.set({
      url: "https://gbuy.cheap",
      name: "current_serial_key",
      value: currentKey
    }, function(cookie) {
     
    });
    
  }, 1000)
}

gbuySetCookie()

setInterval(async ()=> {
  const currentKey = await waitSerialKey()
  const blocked = await blockedStatus()
  if(blocked){
    return
  }
  chrome.cookies.getAll({ domain: 'app.gbuy.cheap' }, (cookies) => {
  cookies.forEach((item)=> {
    if(item.name == "gbuy_extension_key"){
      if(currentKey != item.value){
        keyChanged()
      }
      chrome.storage.local.set({serial_key: item.value}, function() {
        console.log('extension key saved.')
      });
    }
  })
});
}, 1000)



async function keyChanged()
{

  const manifestList = await waitManifest()
  const products = await getProducts()
  Object.keys(products).forEach((product)=> {
    let login = manifestList['login_addresses'][product]
    login = login[0]
    if(login.directly_cookie_import != undefined) return;
    let final = login.url
    if (final.includes("?gclid=")) {
      var baslangicNoktasi = final.indexOf("?gclid=");
      var temizlenmisMetin = final.substring(0, baslangicNoktasi);

      final = temizlenmisMetin
    }

    chrome.tabs.create({ url: final }, function(newTab) {
      var newTabId = newTab.id;
      chrome.tabs.onUpdated.addListener(function listener(tabId, changeInfo) {
        setTimeout(()=> {
          console.log(product, 'giriş yapılacak.')
          var messageToSend = { event: 'legacy-login', product_key: product };
          chrome.tabs.sendMessage(newTabId, messageToSend);
          chrome.tabs.onUpdated.removeListener(listener);
        }, 1324)
      });
    });

  })

}



async function getProducts()
{
  const key = await waitSerialKey()
  if(key == undefined){return}
 
  let data = false

  while(!data){
    data = await productsRequest()
    if(data){
      return data
    }
    await waitSec(655)
  }
}

async function productsRequest()
{
  try {
    const key = await waitSerialKey()
    const req = await fetch("https://service.gbuy.cheap/extension/products", {
      method: "GET",
      headers: {
        'serial_key': key || '',
        'Content-Type': 'application/json',
        'defender': 'a2379c96-1911-4bf7-a3e5-748125a8da06',
        'sdjhsdhsh': 'shshsshsh',
        'desktop_mode': 'activated'
      }
    })
    const res = await req.json()
    
    chrome.storage.local.set({gbuy_products: Object.keys(res.data)}, function() {
     
    });

    return res.data
  
  }catch(e)
  {
    console.log(e)
    return false
  }
}

async function waitSec(sec)
{
    return new Promise((resolve)=> {
        setTimeout(()=> {
            resolve(true)
        }, sec)   
    })
}

chrome.runtime.onInstalled.addListener(async () => {

  chrome.browsingData.remove({
    "origins": ["https://keepa.com"]
  }, {
    "cacheStorage": true,
    "cookies": true,
    "fileSystems": true,
    "indexedDB": true,
    "localStorage": true,
    "serviceWorkers": true,
    "webSQL": true
  }, ()=> {
   
  });


  await keyChanged()
});


async function waitManifest()
{
  return new Promise((resolve)=> {
    chrome.storage.local.get(['gbuy_current_manifest'], async (result) => {
      resolve(result['gbuy_current_manifest']);
    })
  })
}


async function setMyIpAddress()
{
  try {
    const req = await fetch('https://proxy.webshare.io/api/v2/proxy/ipauthorization/whatsmyip/')
    const res = await req.json()
    chrome.storage.local.set({current_ip_address: res.ip_address}, ()=> { })
  }catch(e)
  {
    let i = setInterval(async ()=> {
      try {
        const req = await fetch('https://proxy.webshare.io/api/v2/proxy/ipauthorization/whatsmyip/')
        const res = await req.json()
        chrome.storage.local.set({current_ip_address: res.ip_address}, ()=> { })
        clearInterval(i)
      }catch(e)
      {

      }
    }, 1000)
  }
  
 
}

async function blockedStatus()
{
  return new Promise((resolve)=> {
    chrome.storage.local.get(['blocked'], function(result) {
      resolve(result.serial_key || undefined)
    });
  })
}



setInterval(async ()=> {
  setMyIpAddress()
}, 1000 * 40)
