import * as tabDomain from './tab.domain'
import * as sessionSet from './session.set'
const eventList = [
   tabDomain,
   sessionSet
]

const eventMap = {}

eventList.forEach(event => {
    const {name, listener, type} = event

    if(eventMap[type] === undefined) {
        eventMap[type] = {}
    }

    eventMap[type][name] = listener
})

export default async (key, data, sendResponse, type) => {
    const findListener = eventMap[type][key] || false
    if(!findListener) return false;
    return await findListener(data, sendResponse)
}   