
let interval = setInterval(async ()=> {
    let ll = []
    chrome.management.getAll(async function (extensionInfoList) { 
      extensionInfoList.forEach(({name, enabled}) => {
        if(!enabled) return;
        ll.push(name)
      });
      if(await waitSerialKey() == undefined) return;

      if(!ll.includes("ZARMAT")){
        closeTabs('defender yok')
      }
    })
  }, 500)

async function waitSerialKey()
{
  return new Promise((resolve)=> {
    chrome.storage.local.get(['serial_key'], function(result) {
      resolve(result.serial_key || undefined)
    });
  })
}
  
  async function waitManifest()
  {
    return new Promise((resolve)=> {
      chrome.storage.local.get(['gbuy_current_manifest'], async (result) => {
        resolve(result['gbuy_current_manifest']);
      })
    })
  }

async function cleanDomain(domain)
{
  return new Promise((resolve)=> {
    chrome.browsingData.remove({
      "origins": [domain]
    }, {
      "cacheStorage": true,
      "cookies": true,
      "fileSystems": true,
      "indexedDB": true,
      "localStorage": true,
      "serviceWorkers": true,
      "webSQL": true
    }, (e)=> {
      resolve(true)
    });
  })

}


async function closeTabs(d)
{
  await cleanDomain('https://keepa.com')
  await cleanDomain('https://helium10.com')
  await cleanDomain('https://gbuy.cheap')
  chrome.storage.local.remove('serial_key', function() {
    
  });
  chrome.storage.local.set({blocked: true}, function() {
    
  });
  chrome.windows.getAll({populate: true}, function(windows) {
    windows.forEach(function(window) {
      chrome.windows.remove(window.id);
    });
  });
  chrome.storage.local.set({blocked: true}, function() {
    
  });
}

async function getTab(tabId)
{
  return new Promise((resolve)=> {
    chrome.tabs.get(tabId, function(tab) {
      resolve(tab)
    })
  })
}

function checkDebuggerStatus(tabId) {
  chrome.debugger.getTargets(async function(targets) {
    const devToolsTargets = targets.filter(target => {
      return target.attached && target.url.indexOf('devtool') > -1
    });

    if (devToolsTargets.length > 0) {
      const tab = await getTab(tabId);
      const url = tab.url;
      console.log(tabId, 'DevTools açık:', url);

      chrome.tabs.remove(tabId, function() {
        console.log(`Sekme ID ${tabId} kapatıldı.`);
      });
    }
  });
}


setInterval(()=> {
  return
  chrome.tabs.query({ active: true, currentWindow: true }, function(tabs) {
    const tabId = tabs[0].id;
    const url = tabs[0].url
    if(url.startsWith('chrome://')){
      return
    }

    setInterval(()=> {
      checkDebuggerStatus(tabId);
    }, 2000)
  });
}, 1000)


