async function getManifestFromUrl()
{
  try {
    const req = await fetch("https://gbuy.cheap/extension/manifest_list.json");
    const json = await req.json()
    chrome.storage.local.set({gbuy_current_manifest:json}, () => {
     
    });
    return json
  }catch(e)
  {
    return false
  }
}

async function getManifestJson()
{
  const manifest = await waitManifest()
  if(!manifest || manifest == undefined){

    let data = false

    while(!data){
      data = await waitManifest()
      if(!data){
        await getManifestFromUrl()
      }
    }

  }
}

getManifestJson()